import * as React from 'react';
import { Link } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import Layout from '../components/layout';
import Seo from '../components/seo';

const TermsOfServicePage = () => {
  return (
    <Layout pageTitle="ご利用規約">
      <Seo
        pageTitle="ご利用規約"
        pageDescription="月の森デザイン（ツキノラボ）のご利用規約です。"
      />
      <StaticImage
        alt="ご面倒さまですが、ご一読ください。"
        src="../img/page-terms-of-service.png"
        placeholder="Blurred"
      />

      <section>
        <h2>ご利用について</h2>
        <p>「<Link to="/">moon-forest-design.github.io</Link>」は、月の森デザインが運営しているWebサイトです。</p>

        <p>当サイトをご利用になる場合は、以下に示す「ご利用規約（以下「本規約」と示します）」、及び別途定める「<Link to="/privacy-policy">プライバシーポリシー</Link>」を事前にご覧いただいて、ご同意くださいますようお願い申しあげます。</p>

        <p>また、本規約は、利用者の承諾なしに適宜変更できるものとします。変更後の規約は、当サイトに掲載した時点で効力が発生します。</p>
      </section>

      <section>
        <h2>ご利用上の注意点</h2>
        <section>
          <h3>著作権・知的財産権等について</h3>
          <p>当サイトに掲載している著作物（文章・写真・イラスト・映像等）において、独自に制作したものについての著作権は、当サイトの運営責任者に帰属します。</p>

          <p>当サイトが著作権を有する著作物は、著作権法で保護されています。著作権者の許諾なく、転載・複製・頒布・改変・切り取り等の行為は禁じられています。</p>

          <p>また、当サイトにおいても著作権や肖像権等を侵害するつもりはありません。不手際により何か問題がございましたら、<a href="https://moon-forest-design.com/commission" title="ホームページ制作の「月の森デザイン」へのお問い合せ" target="_blank" rel="noopener noreferrer">月の森デザインのお問い合わせページ</a>よりご連絡いただければ、可能なかぎり迅速に対応させていただきます。</p>
        </section>

        <section>
          <h3>禁止事項について</h3>
          <p>当サイトをご利用になる場合は、次の行為を禁止します。</p>

          <ol>
            <li>著作権・知的財産権を侵害する等の日本国が定める法律・法令に違反する行為</li>
            <li>公序良俗に反する行為、社会通念・倫理・道徳において不適切と判断される行為</li>
            <li>不正アクセス、成りすまし、またはこれらを試みることで業務に支障をきたす行為</li>
            <li>誹謗中傷、嫌がらせ、いじめ等の人格や尊厳を深く傷つける行為</li>
          </ol>
        </section>

        <section>
          <h3>当サイトの運用中止・停止について</h3>
          <p>次の事象が発生した場合、利用者への事前の通知・承諾なく運用を中止・停止することがあります。また、それにより利用者が損害を被ったとしても、当サイトは一切の責任を負わないものとします。</p>

          <ol>
            <li>コンピュータシステムの定期的な保守点検、及び緊急のメンテナンスが発生した場合</li>
            <li>天災・火災・停電・その他不可抗力による理由において、コンピュータシステムの運用が困難になった場合</li>
            <li>人為的災害、第三者からの不正アクセス、コンピュータウィルスの感染等により、コンピュータシステムの運用が困難になった場合</li>
            <li>行政・司法機関から要請があった場合</li>
            <li>当サイトが定める規約に違反する利用者によって業務遂行が著しく困難になった場合</li>
            <li>その他、前項に含まない想定外の問題が起きた場合</li>
          </ol>
        </section>

        <section>
          <h3>免責事項について</h3>
          <p>当サイトは、以下について、利用者に対して一切の責任を負わないものとします。</p>

          <ol>
            <li>当サイトに関する情報の完全性・正確性・安全性・最新性・有用性・適合性等における損害</li>
            <li>当サイトの閲覧時に発生したソフトウェア、ハードウェアの故障、及び損害</li>
            <li>当サイトの中止・停止による事故、及び損害</li>
            <li>当サイトの内容が予告なく削除・修正・追加されたことで生じる損害</li>
            <li>当サイトに掲載されているリンク先のサイトで提供される情報やサービス等で起きた第三者からの苦情、及び賠償</li>
          </ol>
        </section>

        <section>
          <h3>アクセス解析とCookie（クッキー）について</h3>
          <p>当サイトは、利用者がより良い情報を享受できるようコンテンツを最適化するために Google Analyticsを使用しています。Google Analyticsとは、Google社が提供する Webサービスの一つであり、アクセス解析を行うためのツールです。</p>

          <p>Google Analyticsは、Webサイト利用者のアクセスログを匿名で収集するために「Cookie」という技術を利用しています。または、ログ自体を「Cookie」と呼ぶこともあります。</p>

          <p>当サイトを閲覧することで発生するCookie（アクセスログ）は、利用者が使用しているコンピューターの記憶装置（メモリ、ハードディスク、SSD等）に格納され、Google Analyticsを運用している Google社管轄のサーバー内で統計処理のために利用されます。</p>

          <p>また、スマホ等のモバイル端末で閲覧している場合には、識別情報（AndroidやiOSの広告識別子等）のログも収集されることがあります。</p>

          <p>当サイトの Google Analyticsによるログ収集の目的は、利用者が求める情報の傾向を大まかに把握するためであり、個人を特定または追跡するような意図は微塵もなく、プライバシーを侵害する考えは万に一つもありません。</p>

          <p>また、当サイトは、Google Analyticsを使用する上で必要となる Google社が定める「<a href="https://policies.google.com/technologies/partner-sites" target="_blank" rel="noopener noreferrer">Google ポリシーと規約</a>」及び「<a href="https://marketingplatform.google.com/about/analytics/terms/jp/" target="_blank" rel="noopener noreferrer">Google アナリティクス利用規約（7. プライバシー）</a>」の遵守に努めています。</p>

          <p>しかしながら、当サイトの目的がどうであれ、Google Analyticsによる Cookieの収集に不満を感じるのであれば、これを拒否することが可能です。</p>

          <p>多くのブラウザでは、初期設定で Cookieが使用される設定になっています。Cookieを残したくなければ、ブラウザの設定を変更するか、「<a href="https://tools.google.com/dlpage/gaoptout" target="_blank" rel="noopener noreferrer">Google アナリティクス オプトアウト アドオン</a>」などの拡張機能の利用をご検討ください。お手間様ですがよろしくお願いします。</p>
        </section>

        <section>
          <h3>リンクについて</h3>
          <p>当サイトは、基本的にリンクフリーです。次の留意点をご確認の上でリンクしてください。よろしくお願いします。</p>

          <ol>
            <li>当サイトへリンクするために、許可や連絡をしていただく必要はありません。</li>
            <li>基本的にリンクフリーではありますが、例外として公序良俗に反するサイトからのリンクはお断りしています。ご理解いただきたくよろしくお願いします。</li>
            <li>当サイトのコンテンツや URLは永続的な維持をお約束するものではなく、予告なしに削除または変更する場合があります。リンク切れについてご連絡を差しあげることもございません。</li>
            <li>リンクをはる際は、当サイトへのリンクであることが明確に分かるようにしてください。インラインフレームを使用したり、画像等のコンテンツへの直リンクはご遠慮ください。</li>
          </ol>
        </section>

      </section>

      <section>
        <h2>推奨環境について</h2>
        <section>
          <h3>ブラウザ</h3>
          <p>安全でより良い状態での閲覧を望むのであれば、最新版のブラウザの利用をお勧めします。（以下、対応ブラウザ）</p>

          <ul>
            <li>Mozilla Firefox</li>
            <li>Google Chrome・モバイル版Chrome</li>
            <li>Apple Safari・モバイル版Safari</li>
            <li>Microsoft Edge</li>
          </ul>
        </section>

        <section>
        <h3>JavaScript</h3>
          <p>当サイトでは、JavaScriptを使用しています。JavaScriptとは、プログラミング言語の一つで、利用者がWebサイトを閲覧しやすいよう補助する等、利便性を高めるために使用されています。</p>

          <p>当サイトのコンテンツをより良くご利用いただくためには、ブラウザの JavaScriptの設定を無効にしないことをお勧めします。</p>
        </section>

      </section>

      <section>
        <h2>お問い合わせについて</h2>
        <p>個人情報の取扱いに関するお問い合せは、<a href="https://moon-forest-design.com/commission" title="ホームページ制作の「月の森デザイン」へのお問い合せ" target="_blank" rel="noopener noreferrer">月の森デザインのメールフォーム</a>よりご連絡ください。</p>
      </section>

      <section>
        <h2>運営者情報</h2>
        <p>当サイトの運営者情報です。</p>

        <figure>
          <table>
            <tbody>
              <tr><th>組織名</th><td>月の森デザイン</td></tr>
              <tr><th>所在地</th><td>山形県 山形市</td></tr>
              <tr><th>運営責任者</th><td>イールド（yield）</td></tr>
              <tr><th>URL</th><td>https://moon-forest-design.github.io/</td></tr>
            </tbody>
          </table>
          <figcaption>運営者情報一覧</figcaption>
        </figure>
      </section>

      <hr />

      <p>発効日: 2021年11月20日</p>

    </Layout>
  )
};

export default TermsOfServicePage;
